<template>
<div class="fx-issue">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-tips">请确认您选择的问题：</div>
    <div class="fx-content" :style="{height:clientHeight+'px'}">
        <div class="wrapper">
           <div class="questionItem" v-show="value.hasQ" v-for="(value,index) in questionJson" :key="index">
                <div v-for="(p,m) in value.children" :key="m">
                    <insure-tree :model="p"></insure-tree>
                </div>
            </div>
        </div>
    </div>
    <div class="fx-btns">
        <div class="preBtn" @click="goPrev()">返回修改</div>
        <div class="nextBtn" @click="goNext()">确定</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import insureTree from "@/components/insureTree/insureTree";
import { Indicator } from 'mint-ui';
export default {
    name:'issue',
    data(){
        return{
            orderId:null,
            headerTitle:'智能核保',
            clientHeight:0,
            questionJson:[],
        }
    },
    created() {

    },
    methods: {
        //头部点击事件
        onHeaderBtn(text){
            this.$router.go(-1);
        },
        //返回修改
        goPrev(){
            let TMP_QUES_ILLS = JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
            this.initList(TMP_QUES_ILLS,2,3);
            this.initList(TMP_QUES_ILLS,2,4);
            sessionStorage.setItem("TMP_QUES_ILLS",JSON.stringify(TMP_QUES_ILLS));
            this.$router.go(-1);
        },
        initList(data,type,level){
            data.map((item,index) => {
                if(item.type==type && item.level==level){
                    this.$set(item,'choosed',2);
                }
                if(item.children && item.children.length>0)this.initList(item.children,type,level);
            });
        },
        //确定
        goNext(){
            //获取选中疾病的答案id
            var tmpIdList = [];
            let findTmpIdList = function(data){
                data.map(item=>{
                    if(item.choosed === 1 && item.type === 3 && item.children.length === 0){
                        tmpIdList.push(item.id);
                        // if(item.children.length>0){
                        //     findTmpIdList(item.children);
                        // }
                    }
                    else if(item.choosed === 1 && item.children.length>0){
                        findTmpIdList(item.children);
                    }
                    else if(item.choosed === 1 && item.children.length === 0){
                        tmpIdList.push(item.id);
                    }
                })
            }
            let QUES_ILLS = JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
            let tmp = findTmpIdList(QUES_ILLS);
            console.log("tmpIdList",tmpIdList);
            this.chosedIllIds = tmpIdList;
            //获取核保结果
            let THAT = this;
            let HEALTHY_TYPE = sessionStorage.getItem("HEALTHY_TYPE") === '1'?2:1;
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
            THAT.$http.getHttp({
                name:'fxResults',
                params:{
                    type:HEALTHY_TYPE,
                    ids:tmpIdList
                },
                orderId:THAT.orderId,
                type:'post'
            },
            res => {
                Indicator.close();
                let status = res.data.status;
                //返回的唯一标识 requestId
                //let requestId = res.data.requestId;
                let errorContent = res.data.errorList;
                if(errorContent){
                    sessionStorage.setItem("errorContent",JSON.stringify(errorContent));
                }
                //sessionStorage.setItem("REQUESTID",requestId);
                let resultType = status === 100?'?resultType=1':status === 101?'?resultType=0':status === 102?'?resultType=2':status === 103 ? '?resultType=4':status === 201 ? '?resultType=3':''
                THAT.$router.push({path:'/fuxing/result'+resultType})
                console.log(res);//100 通过 101 拒保 102 除责 103人工核保 201数据不正确
            })
        },
    },
    computed:{},
    components:{
        "header-text":headerText,
        "insure-tree":insureTree
    },
    mounted(){
        let orderId = sessionStorage.getItem("ORDER_ID");
        this.orderId = orderId;
        let QUES_ILLS = JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
        this.questionJson = QUES_ILLS ;
		console.log(this.questionJson);
        this.clientHeight = window.innerHeight
        -document.getElementsByClassName("fx-btns")[0].clientHeight
        -document.getElementsByClassName("fx-tips")[0].clientHeight;
    }
}
</script>

<style lang="scss">
.fx-issue{
    background:#fff;
    .fx-content{
        overflow: hidden;
        width:100%;
        .wrapper{
            height:100%;
            width:100%;
            overflow: auto;
            background: #eee;
            .questionItem{
                padding-left: r(27);
                background:#fff;
                padding-bottom:r(30);
                margin-bottom: r(15);
                .title{
                    line-height:r(30);
                    padding-top:r(20);
                    padding-bottom:r(20);
                    font-size:r(23);
                    color:#666;
                }
                .subTitle{
                   overflow: hidden;
                }
            }
        }
    }
    .fx-tips{
        height:r(88);
        line-height:r(88);
        font-size:r(30);
        border-bottom:1px solid #dddddd;
        color:#4580f6;
        font-weight: bold;
        padding-left: r(27);
    }
    .fx-btns{
        height:r(92);
        position:fixed;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:50%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .preBtn{
            color:#000;
            background:#fff;
            line-height:r(92);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
    }
}
</style>


