<template>
<div>
    <div v-if="model.type==2 && model.choosed==1">
        <div class="subTitle" v-show="val.choosed==1" v-for="(val) in model.children" :key="val.id">
            <div class="item">{{model.content}}</div> 
            <span v-if="val.content === '是'" class="yes result">{{val.content}}</span>
            <span v-if="val.content !== '是'" class="no result">{{val.content}}</span>
        </div>
        <div v-show="model.children && model.children.length>0"  v-for="(val,i) in model.children" :key="i">
            <insure-tree :model="p" v-show="val.children && val.children.length>0" v-for="p in val.children" :key="p.id"
            ></insure-tree>
        </div>
    </div>
    <div v-else>
        <div v-show="model.children && model.children.length>0" v-for="(p,m) in model.children" :key="m">
			<div class="treeSubItem" >
				<div class="title" v-if="model.choosed ==1 && p.children.length>0">- {{model.content}}</div>
				<insure-tree :model="p" ></insure-tree>
			</div>
        </div>
    </div>
</div>
</template>

<script>
import Vue from "vue";
import insureTree from "@/components/insureTree/insureTree";
export default {
  name:"insure-tree",
  props: {
      model: Object,
    },
    components: {
    "insure-tree": insureTree,
  },
};
</script>

<style lang="scss">
.treeSubItem{
	background:#fff;
	margin-bottom: r(10);
    .subTitle{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
}
  .subTitle{
        position: relative;
        padding-bottom:r(10);
        background:#fff;
        padding-left: r(20);
        padding-top:r(10);
        vertical-align: middle;
		width: 100%;
		overflow: hidden;
        .item{
            width:80%;
            display:inline-block;
            font-size: r(28);
            color:#000;
            line-height:r(42);
            min-height:r(48); 
            vertical-align: middle;
        }
        .result{
            display:inline-block;
            width:20%;
            // position: absolute;
            // right:r(5);
            // top: 50%;
            // transform: translateY(-50%);
            font-size: r(30);
            text-align: center;
            line-height:r(36);
            // height:36rpx;
        }
        .yes{
            color: #4580f6;
        }
        .no{
            color:#e70000;
        }
    }
</style>



